@import "https://use.typekit.net/liz4yul.css";
:root {
  --cno-color-grass: #69813b;
  --cno-color-forest: #41693d;
  --cno-color-apricot: #b96a56;
  --cno-color-cream: #eee8d2;
  --cno-color-iron: #231d1b;
  --cno-color-spruce: #045d67;
  --cno-font-headings: trajan-sans-pro, sans-serif;
  --cno-font-body: gill-sans-nova, Helvetica Neue, Noto Sans, Liberation Sans, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

.main-theme-color {
  color: var(--cno-color-grass) !important;
}

.main-theme-color[_ngcontent-pql-c316] {
  color: var(--cno-color-forest) !important;
}

.secondary-theme-color, .secondary-theme-color[_ngcontent-pql-c316] {
  color: var(--cno-color-apricot) !important;
}

body {
  font-family: var(--cno-font-body) !important;
}

body a {
  color: var(--cno-color-forest) !important;
}

.page-title-section {
  font-family: var(--cno-font-headings);
  font-weight: 400;
}

.main-title {
  font-family: var(--cno-font-headings);
  padding-top: 40px;
  font-weight: bold;
}

.dining-location, .activity-title, .sub-title {
  font-family: var(--cno-font-headings);
  padding-top: 0;
  padding-left: 16px;
  padding-right: 16px;
  font-weight: 300;
}

.dining-location, .dining-main-title {
  display: none !important;
}

.activity-title, .dining-title, .listing-sub-title {
  font-family: var(--cno-font-headings);
}

.activity-list-title, .dining-list-title {
  color: var(--cno-color-grass);
  font-family: var(--cno-font-headings);
  text-decoration: none;
}

.dining-list-title:hover {
  background-color: var(--cno-color-grass) !important;
  color: var(--cno-color-iron) !important;
}

.selected-location-price-top-bar {
  color: var(--cno-color-grass) !important;
}

.sub-menu-selection[_ngcontent-pql-c316] {
  color: var(--cno-color-grass) !important;
  border-bottom-color: var(--cno-color-grass) !important;
}

.activity-buttons, .activity-cancel-buttons {
  background-color: var(--cno-color-grass);
  color: #000;
  letter-spacing: 5px;
  cursor: pointer;
  line-height: 14px;
}

.activity-buttons:hover, .activity-cancel-buttons:hover {
  background-color: var(--cno-color-forest);
}

.activity-confirm-buttons {
  justify-content: center;
  align-items: center;
  width: 230px;
  display: flex;
  padding: 15px 25px !important;
}

.dialog-parent-container .popup-confirm-button {
  background-color: var(--cno-color-grass);
  color: #000;
}

.mat-calendar-body-selected {
  color: #fdfdfd;
  background-color: var(--cno-color-grass) !important;
}

.mat-calendar-body-selected:hover {
  background-color: #e4adc2;
}

.mat-calendar-body-cell:not(.mat-calendar-body-disabled):hover > .mat-calendar-body-cell-content:not(.mat-calendar-body-selected):not(.mat-calendar-body-comparison-identical) {
  color: #fff;
  background-color: var(--cno-color-apricot) !important;
}

.dining-select-prev-date, .dining-select-next-date {
  font-size: 25px;
  color: var(--cno-color-primary) !important;
}

::ng-deep .mat-checkbox-indeterminate.mat-accent .mat-checkbox-background {
  background-color: var(--cno-color-primary) !important;
}

::ng-deep .mat-checkbox-checked.mat-accent .mat-checkbox-background {
  background-color: var(--cno-color-primary) !important;
}

mat-select.mat-select > div.mat-select-trigger > div.mat-select-arrow-wrapper > div.mat-select-arrow {
  color: var(--cno-color-primary) !important;
}

mat-option.mat-option > span.mat-option-text {
  color: #000000de;
}

::ng-deep .mat-form-field-appearance-outline.mat-focused .mat-form-field-outline-thick {
  color: var(--cno-color-primary) !important;
}

.activites-header {
  background: #fff !important;
}

.mat-icon-color, .mat-icon-color[_ngcontent-pql-c208] {
  color: var(--cno-color-grass) !important;
}

.legend-icon.selected {
  background-color: var(--cno-color-apricot) !important;
}

.cart-logo {
  color: var(--cno-color-grass) !important;
}

.cart-count {
  color: var(--cno-color-moss) !important;
  border: 1px solid var(--cno-color-grass) !important;
}

.location-detail {
  background-color: var(--cno-color-cream);
  color: var(--cno-color-iron);
}

.location-detail__header-label.available {
  background-color: var(--cno-color-forest);
  color: #fff;
}

.header-right-section cd-timer {
  background-color: var(--cno-color-grass) !important;
}

.header-right-section cd-timer .time-left-text {
  color: var(--cno-color-grass) !important;
}

.order-summary-short-detail-section[_ngcontent-pql-c316] {
  background-color: var(--cno-color-forest) !important;
  color: #fff !important;
}

.order-summary-spacer[_ngcontent-pql-c316] {
  background-color: #fff !important;
}

.order-summary-grand-total[_ngcontent-pql-c316]:not(.highlightSection) {
  background-color: var(--cno-color-spruce) !important;
  color: #fff !important;
}
